export class HistoryItemQuery {
    public readonly id: number;
    public readonly hasDetails: boolean;
    public readonly newest: number;

    constructor(id: number, hasDetails: boolean = false, newest: number = 0) {
        this.id = id;
        this.hasDetails = hasDetails;
        this.newest = newest;
    }

    toObject(): { [key: string]: string } {
        return {
            id: this.id.toString(),
            hasDetails: this.hasDetails.toString(),
            newest: this.newest.toString()
        };
    }
}