import { AuthenticationContext } from "@axa-fr/react-oidc-context";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { User } from "oidc-client";
import React from "react";
import { ItemQuery } from "../dataContracts/ItemQuery";
import { PricedItem } from "../dataContracts/PricedItem";
import ApiService from "../services/ApiService";
import { ItemField, ItemFieldSort } from "../utils/ItemField";
import { ItemRarity } from "../utils/ItemRarity";
import { ItemType } from "../utils/ItemType";
import RarityMultiSelect from "./RarityMultiSelect";
import TypeMultiSelect from "./TypeMultiSelect";

export type SortingBarProps = {
    name: string;
    onLoadingStart(): void;
    onLoadingStop(): void;
    children?: (sortedItems: PricedItem[]) => React.ReactNode | null;
}

export type SortingBarState = {
    user: User | undefined;
    pendingQuery: ItemQuery;
    currentQuery: ItemQuery;
    filteredItems: PricedItem[];
    isFilterOpen: boolean;
    currentPage: number;
    totalPages: number;
}

export default class SortingBar extends React.Component<SortingBarProps, SortingBarState> {
    constructor(props: SortingBarProps) {
        super(props);

        this.state = {
            user: undefined,
            pendingQuery: new ItemQuery(),
            currentQuery: new ItemQuery(),
            filteredItems: [],
            isFilterOpen: false,
            currentPage: 1,
            totalPages: 1
        };

        this.resetQueryValues = this.resetQueryValues.bind(this);
        this.toggleFilter = this.toggleFilter.bind(this);
        this.onFilterOpen = this.onFilterOpen.bind(this);
        this.onFilterClose = this.onFilterClose.bind(this);
        this.onFilter = this.onFilter.bind(this);
        this.onSearch = this.onSearch.bind(this);
        this.onToPage = this.onToPage.bind(this);
        this.onFirstPage = this.onFirstPage.bind(this);
        this.onLastPage = this.onLastPage.bind(this);
        this.onNextPage = this.onNextPage.bind(this);
        this.onPrevPage = this.onPrevPage.bind(this);

        this.onNameChange = this.onNameChange.bind(this);
        this.onTypeChange = this.onTypeChange.bind(this);
        this.onRarityChange = this.onRarityChange.bind(this);
        this.onMinLevelChange = this.onMinLevelChange.bind(this);
        this.onMaxLevelChange = this.onMaxLevelChange.bind(this);
        this.onMinBuyPriceChange = this.onMinBuyPriceChange.bind(this);
        this.onMaxBuyPriceChange = this.onMaxBuyPriceChange.bind(this);
        this.onMinSellPriceChange = this.onMinSellPriceChange.bind(this);
        this.onMaxSellPriceChange = this.onMaxSellPriceChange.bind(this);
        this.onSort = this.onSort.bind(this);
        this.onSortDirection = this.onSortDirection.bind(this);
    }

    resetQueryValues() {
        this.setState({pendingQuery: new ItemQuery()});
    }

    toggleFilter() {
        const { isFilterOpen } = this.state;
        if (isFilterOpen)
            this.onFilterClose();
        else
            this.onFilterOpen();
    }

    onFilterOpen() {
        this.setState({isFilterOpen: true});
    }

    onFilterClose() {
        this.setState({isFilterOpen: false});
    }

    async onFilter() {
        const { onLoadingStart, onLoadingStop } = this.props;
        const { user, pendingQuery } = this.state;

        if (!user)
            return;

        this.setState({filteredItems: []}, async () => {
            const totalPages = await ApiService.GetItemPageCount(user.access_token, pendingQuery);
            if (totalPages <= 0)
                return;       
    
            onLoadingStart(); 
        
            const items = await ApiService.GetItems(user.access_token, pendingQuery, 1) ?? [];
            this.setState({filteredItems: items, currentQuery: pendingQuery, currentPage: 1, totalPages: totalPages}, onLoadingStop);
        });
    }

    async onSearch(event: React.FormEvent<HTMLFormElement>) {
        event.preventDefault();
        const { onLoadingStart, onLoadingStop } = this.props;
        const { user, pendingQuery } = this.state;

        if (!user)
            return;

        const nameOnlyQuery = new ItemQuery({name: pendingQuery.name});
        this.setState({filteredItems: []}, async () => {
            const totalPages = await ApiService.GetItemPageCount(user.access_token, nameOnlyQuery);
            if (totalPages <= 0)
                return;  
    
            onLoadingStart();
            
            const items = await ApiService.GetItems(user.access_token, nameOnlyQuery, 1) ?? [];
            this.setState({filteredItems: items, currentQuery: nameOnlyQuery, currentPage: 1, totalPages: totalPages}, onLoadingStop);
        });
    }

    async onToPage(pageNumber: number) {
        const { onLoadingStart, onLoadingStop } = this.props;
        const { user, currentQuery, totalPages } = this.state;

        if (!user)
            return;

        if (pageNumber <= 0 || pageNumber > totalPages)
            return;

        this.setState({filteredItems: []}, async () => {
            onLoadingStart();
    
            const items = await ApiService.GetItems(user.access_token, currentQuery, pageNumber) ?? [];
            this.setState({filteredItems: items, currentPage: pageNumber}, onLoadingStop);
        });
    }

    async onFirstPage() {
        await this.onToPage(1);
    }

    async onLastPage() {
        const { totalPages } = this.state;
        await this.onToPage(totalPages);
    }

    async onNextPage() {
        const { onLoadingStart, onLoadingStop } = this.props;
        const { user, currentQuery, currentPage, totalPages } = this.state;

        if (!user)
            return;

        if (currentPage >= totalPages)
            return;

        
        this.setState({filteredItems: []}, async () => {
            onLoadingStart();
    
            const items = await ApiService.GetItems(user.access_token, currentQuery, currentPage + 1) ?? [];
            this.setState({filteredItems: items, currentPage: currentPage + 1}, onLoadingStop);
        });
    }

    async onPrevPage() {
        const { onLoadingStart, onLoadingStop } = this.props;
        const { user, currentQuery, currentPage } = this.state;

        if (!user)
            return;

        if (currentPage <= 1)
            return;

        this.setState({filteredItems: []}, async () => {
            onLoadingStart();
            
            const items = await ApiService.GetItems(user.access_token, currentQuery, currentPage - 1) ?? [];
            this.setState({filteredItems: items, currentPage: currentPage - 1}, onLoadingStop);
        });
    }

    onNameChange(event: React.ChangeEvent<HTMLInputElement>) {
        const value = event.target.value;
        this.setState(prevState => {
            const query = prevState.pendingQuery;
            query.name = value;
            return {pendingQuery: query};
        });
    }
    
    onTypeChange(newType: ItemType, callback?: (() => void) | undefined) {
        this.setState(prevState => {
            const query = prevState.pendingQuery;
            query.type = newType;
            return {pendingQuery: query};
        }, callback);
    }
    
    onRarityChange(newRarity: ItemRarity, callback?: (() => void) | undefined) {
        this.setState(prevState => {
            const query = prevState.pendingQuery;
            query.rarity = newRarity;
            return {pendingQuery: query};
        }, callback);
    }

    onMinLevelChange(event: React.ChangeEvent<HTMLInputElement>) {
        let value = parseInt(event.target.value ?? "0");
        if (value < 0)
            value = 0;
        if (value > 80)
            value = 80;
        this.setState(prevState => {
            const query = prevState.pendingQuery;
            if (value > query.maxLevel)
                return {pendingQuery: query};

            query.minLevel = value;
            return {pendingQuery: query};
        });
    }

    onMaxLevelChange(event: React.ChangeEvent<HTMLInputElement>) {
        let value = parseInt(event.target.value ?? "0");
        if (value < 0)
            value = 0;
        if (value > 80)
            value = 80;
        this.setState(prevState => {
            const query = prevState.pendingQuery;
            if (value < query.minLevel)
                return {pendingQuery: query};

            query.maxLevel = value;
            return {pendingQuery: query};
        });
    }

    onMinBuyPriceChange(event: React.ChangeEvent<HTMLInputElement>) {
        let value = parseInt(event.target.value ?? "0");
        if (value < 0)
            value = 0;
        this.setState(prevState => {
            const query = prevState.pendingQuery;
            query.minBuyPrice = value;
            return {pendingQuery: query};
        });
    }

    onMaxBuyPriceChange(event: React.ChangeEvent<HTMLInputElement>) {
        let value = parseInt(event.target.value ?? "0");
        if (value < 0)
            value = 0;
        this.setState(prevState => {
            const query = prevState.pendingQuery;
            query.maxBuyPrice = value;
            return {pendingQuery: query};
        });
    }

    onMinSellPriceChange(event: React.ChangeEvent<HTMLInputElement>) {
        let value = parseInt(event.target.value ?? "0");
        if (value < 0)
            value = 0;
        this.setState(prevState => {
            const query = prevState.pendingQuery;
            query.minSellPrice = value;
            return {pendingQuery: query};
        });
    }

    onMaxSellPriceChange(event: React.ChangeEvent<HTMLInputElement>) {
        let value = parseInt(event.target.value ?? "0");
        if (value < 0)
            value = 0;
        this.setState(prevState => {
            const query = prevState.pendingQuery;
            query.maxSellPrice = value;
            return {pendingQuery: query};
        });
    }

    onSort(event: React.ChangeEvent<HTMLSelectElement>) {
        const value = event.target.value;
        this.setState(prevState => {
            const query = prevState.pendingQuery;
            query.sortBy = parseInt(value) as ItemField;
            return {pendingQuery: query};
        }, this.onFilter);
    }

    onSortDirection() {
        this.setState(prevState => {
            const query = prevState.pendingQuery;
            query.sortDirection = query.sortDirection === ItemFieldSort.Ascending ? ItemFieldSort.Descending : ItemFieldSort.Ascending;
            return {pendingQuery: query};
        }, this.onFilter);
    }

    render() {
        const { name, children } = this.props;
        const { pendingQuery, filteredItems, isFilterOpen, currentPage, totalPages } = this.state;

        const isSortAscending: boolean = pendingQuery.sortDirection === ItemFieldSort.Ascending;
        const pageNums: number[] = [];
        
        for (var i = currentPage < 5 ? 1 : currentPage - 4; 
            i <= ((currentPage + 4) <= totalPages ? currentPage + 4 : totalPages); 
            i++) {
            pageNums.push(i);
        }

        return(
            <>
                <div className="flex flex-row justify-between items-center px-4 py-2 mb-4 bg-gray-900 text-gray-500">
                    <AuthenticationContext.Consumer>
                        {props => {
                            if (props.oidcUser != null && !this.state.user)
                                this.setState({user: props.oidcUser}, this.onFilter);

                            return(
                                <></>
                            );
                        }}
                    </AuthenticationContext.Consumer>
                    <span className="">{name}</span>
                    <div className="flex items-center">
                        <form className="flex flex-row mr-4"
                            onSubmit={this.onSearch}>
                            <input type="text" className="pl-2 rounded-l bg-gray-600 text-gray-200"
                                value={pendingQuery.name}
                                onChange={this.onNameChange} />
                            <button className="transition duration-150 ease-in-out flex items-center px-2 rounded-r bg-gray-700 text-gray-300 hover:bg-gray-800 hover:text-gray-200 hover:shadow-lg">
                                <FontAwesomeIcon icon="search" className="mr-1 text-xs" />
                                <span className="text-xs">Search</span>
                            </button>
                        </form>
                        <label htmlFor="" className="mr-2 text-sm">Sort By</label>
                        <select name="" id="" className="pl-2 rounded-l h-6 text-sm bg-gray-700 text-gray-300" 
                            value={pendingQuery.sortBy} 
                            onChange={this.onSort}>
                            <option value={ItemField.Name} className="">{ItemField[ItemField.Name]}</option>
                            <option value={ItemField.Buys} className="">{ItemField[ItemField.Buys]}</option>
                            <option value={ItemField.Sells} className="">{ItemField[ItemField.Sells]}</option>
                            <option value={ItemField.Appeal} className="">{ItemField[ItemField.Appeal]}</option>
                            <option value={ItemField.Margin} className="">{ItemField[ItemField.Margin]}</option>
                            <option value={ItemField.Sold} className="">{ItemField[ItemField.Sold]}</option>
                            <option value={ItemField.Bought} className="">{ItemField[ItemField.Bought]}</option>
                        </select>
                        <button className="transition duration-150 ease-in-out px-2 mr-4 rounded-r border-l border-gray-800 bg-gray-700 text-gray-300 hover:bg-gray-800 hover:text-gray-200 hover:shadow-lg"
                            onClick={this.onSortDirection}>
                            {(isSortAscending && pendingQuery.sortBy !== ItemField.Name &&
                                <FontAwesomeIcon icon="sort-numeric-up" />
                            ) ||
                            (!isSortAscending && pendingQuery.sortBy !== ItemField.Name &&
                                <FontAwesomeIcon icon="sort-numeric-down-alt" />
                            ) ||
                            (isSortAscending && pendingQuery.sortBy === ItemField.Name &&
                                <FontAwesomeIcon icon="sort-alpha-up" />
                            ) ||
                            (!isSortAscending && pendingQuery.sortBy === ItemField.Name &&
                                <FontAwesomeIcon icon="sort-alpha-down-alt" />
                            )}
                        </button>
                        <div className="relative">
                            <button className="transition duration-150 ease-in-out mr-4 flex items-center rounded bg-gray-700 text-gray-300 hover:bg-gray-800 hover:text-gray-200 hover:shadow-lg" style={{padding: "0.20rem 0.5rem"}}
                                onClick={this.toggleFilter}>
                                <FontAwesomeIcon icon="filter" className="mr-1 text-xs" />
                                <span className="text-xs">Filter</span>
                            </button>
                            {(isFilterOpen && 
                                <div className="absolute flex flex-col px-3 py-3 w-64 filter-arrow-up rounded shadow-lg bg-gray-900" style={{height: "21.7rem", top: "2.75rem", right: "0rem", zIndex: 100}}>
                                    <div className="mb-2 flex flex-row">
                                        <label htmlFor="" className="px-2 py-1 rounded-l text-xs bg-gray-700 text-gray-300">Name</label>
                                        <input type="text" className="w-full px-2 rounded-r bg-gray-600 text-gray-200"
                                            value={pendingQuery.name}
                                            onChange={this.onNameChange} />
                                    </div>
                                    <div className="mb-2 flex flex-row">
                                        <label htmlFor="" className="px-2 py-1 rounded-l text-xs bg-gray-700 text-gray-300">Type</label>
                                        <TypeMultiSelect type={pendingQuery.type} onChange={this.onTypeChange} />
                                    </div>
                                    <div className="mb-2 flex flex-row">
                                        <label htmlFor="" className="px-2 py-1 rounded-l text-xs bg-gray-700 text-gray-300">Rarity</label>
                                        <RarityMultiSelect rarity={pendingQuery.rarity} onChange={this.onRarityChange} />
                                    </div>
                                    <div className="mb-2 flex flex-col">
                                        <span className="mb-1 text-sm">Level</span>
                                        <div className="flex flex-row">
                                            <div className="mr-1 flex flex-row">
                                                <label htmlFor="" className="px-2 py-1 rounded-l text-xs bg-gray-700 text-gray-300">Min</label>
                                                <input type="number" className="w-full px-2 rounded-r text-sm bg-gray-600 text-gray-200"
                                                    value={pendingQuery.minLevel}
                                                    onChange={this.onMinLevelChange} />
                                            </div>
                                            <div className="flex flex-row">
                                                <label htmlFor="" className="px-2 py-1 rounded-l text-xs bg-gray-700 text-gray-300">Max</label>
                                                <input type="number" className="w-full px-2 rounded-r text-sm bg-gray-600 text-gray-200"
                                                    value={pendingQuery.maxLevel}
                                                    onChange={this.onMaxLevelChange} />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="mb-2 flex flex-col">
                                        <span className="mb-1 text-sm">Buy Price</span>
                                        <div className="flex flex-row">
                                            <div className="mr-1 flex flex-row">
                                                <label htmlFor="" className="px-2 py-1 rounded-l text-xs bg-gray-700 text-gray-300">Min</label>
                                                <input type="number" className="w-full px-2 rounded-r text-sm bg-gray-600 text-gray-200"
                                                    value={pendingQuery.minBuyPrice}
                                                    onChange={this.onMinBuyPriceChange} />
                                            </div>
                                            <div className="flex flex-row">
                                                <label htmlFor="" className="px-2 py-1 rounded-l text-xs bg-gray-700 text-gray-300">Max</label>
                                                <input type="number" className="w-full px-2 rounded-r text-sm bg-gray-600 text-gray-200"
                                                    value={pendingQuery.maxBuyPrice}
                                                    onChange={this.onMaxBuyPriceChange} />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="mb-4 flex flex-col">
                                        <span className="mb-1 text-sm">Sell Price</span>
                                        <div className="flex flex-row">
                                            <div className="mr-1 flex flex-row">
                                                <label htmlFor="" className="px-2 py-1 rounded-l text-xs bg-gray-700 text-gray-300">Min</label>
                                                <input type="number" className="w-full px-2 rounded-r text-sm bg-gray-600 text-gray-200"
                                                    value={pendingQuery.minSellPrice}
                                                    onChange={this.onMinSellPriceChange} />
                                            </div>
                                            <div className="flex flex-row">
                                                <label htmlFor="" className="px-2 py-1 rounded-l text-xs bg-gray-700 text-gray-300">Max</label>
                                                <input type="number" className="w-full px-2 rounded-r text-sm bg-gray-600 text-gray-200"
                                                    value={pendingQuery.maxSellPrice}
                                                    onChange={this.onMaxSellPriceChange} />
                                            </div>
                                        </div>
                                    </div>

                                    <div className="flex flex-row justify-between">
                                        <div className="">
                                            <button className="transition duration-150 ease-in-out px-2 py-1 rounded text-sm bg-red-700 text-red-300 hover:shadow-lg hover:bg-red-800 hover:text-red-200"
                                                onClick={this.resetQueryValues}>
                                                Reset
                                            </button>
                                        </div>
                                        <div className="flex flex-row-reverse">
                                            <button className="transition duration-150 ease-in-out ml-2 px-2 py-1 rounded text-sm bg-indigo-700 text-indigo-300 hover:shadow-lg hover:bg-indigo-800 hover:text-indigo-200"
                                                onClick={async () => {
                                                    await this.onFilter(); 
                                                    this.onFilterClose();
                                                }}>
                                                Filter
                                            </button>
                                            <button className="transition duration-150 ease-in-out px-2 py-1 rounded text-sm bg-gray-700 text-gray-300 hover:shadow-lg hover:bg-gray-800 hover:text-gray-200"
                                                onClick={this.onFilterClose}>
                                                Cancel
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            )}
                        </div>
                        <button className="transition duration-150 ease-in-out flex items-center rounded bg-gray-700 text-gray-300 hover:bg-gray-800 hover:text-gray-200 hover:shadow-lg" style={{padding: "0.3rem 0.5rem"}}
                            onClick={this.onFilter}>
                            <FontAwesomeIcon icon="redo-alt" className="text-sm" />
                        </button>
                    </div>
                </div>
                {children != null ? children(filteredItems): null}
                {totalPages > 0 && (
                    <div className="mt-4 flex flex-row-reverse w-full">
                        <div className="flex flex-row">
                            <button className="transition duration-150 ease-in-out mr-4 px-2 py-1 flex items-center rounded bg-gray-700 text-gray-300 hover:bg-gray-800 hover:text-gray-200 hover:shadow-lg"
                                onClick={this.onFirstPage}>
                                <FontAwesomeIcon icon="angle-double-left" className="mr-1 text-xs" />
                                <span className="">First</span>
                            </button>
                            <button className="transition duration-150 ease-in-out mr-4 px-2 py-1 flex items-center rounded bg-gray-700 text-gray-300 hover:bg-gray-800 hover:text-gray-200 hover:shadow-lg"
                                onClick={this.onPrevPage}>
                                <FontAwesomeIcon icon="arrow-left" className="mr-1 text-xs" />
                                <span className="">Prev</span>
                            </button>
                            {pageNums.map((value, index) => (
                                <button key={index} disabled={currentPage === value} 
                                    className={`transition duration-150 ease-in-out mr-4 px-2 py-1 flex items-center rounded ${currentPage === value ? "bg-gray-900 text-gray-200 cursor-default" : "bg-gray-700 text-gray-300 hover:bg-gray-900 hover:text-gray-200 hover:shadow-lg"}`}
                                    onClick={() => this.onToPage(value)}>
                                    {value}
                                </button>
                            ))}
                            <button className="transition duration-150 ease-in-out mr-4 px-2 py-1 flex items-center rounded bg-gray-700 text-gray-300 hover:bg-gray-800 hover:text-gray-200 hover:shadow-lg"
                                onClick={this.onNextPage}>
                                <span className="mr-1">Next</span>
                                <FontAwesomeIcon icon="arrow-right" className="text-xs" />
                            </button>
                            <button className="transition duration-150 ease-in-out px-2 py-1 flex items-center rounded bg-gray-700 text-gray-300 hover:bg-gray-800 hover:text-gray-200 hover:shadow-lg"
                                onClick={this.onLastPage}>
                                <span className="mr-1">Last</span>
                                <FontAwesomeIcon icon="angle-double-right" className="text-xs" />
                            </button>
                        </div>
                    </div>
                )}
            </>
        );
    }
}