import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import { ItemType } from "../utils/ItemType";

export type TypeMultiSelectProps = {
    type: ItemType;
    onChange(type: ItemType, callback?: (() => void) | undefined): void;
} 

export type TypeMultiSelectState = {
    isOpen: boolean;
}

export default class TypeMultiSelect extends React.Component<TypeMultiSelectProps, TypeMultiSelectState> {
    constructor(props: TypeMultiSelectProps) {
        super(props);

        this.state = {
            isOpen: false
        };

        this.toggleOpen = this.toggleOpen.bind(this);
        this.toggleType = this.toggleType.bind(this);
        this.checkType = this.checkType.bind(this);
    }

    private static isPowerOf2(type: ItemType): boolean {
        const typeNum = type as number;
        return (typeNum !== 0) && ((typeNum & (typeNum - 1)) === 0);
    }

    private toggleOpen() {
        const { isOpen } = this.state;
        if (isOpen)
            this.setState({isOpen: false});
        else
            this.setState({isOpen: true}, );
    }

    private toggleType(otherType: ItemType) {
        const { type, onChange } = this.props;
        let newType: ItemType;
        switch (otherType) {
        case ItemType.None:
        case ItemType.All:
            newType = otherType;
            break;
        default:
            newType = type ^ otherType;
            break;
        }

        onChange(newType, () => this.setState({isOpen: true}));
    }

    private checkType(otherType: ItemType): boolean {
        const { type } = this.props;
        if (type !== ItemType.All && otherType === ItemType.All)
            return false;
        return (type & otherType) !== 0;
    }
    
    render() {
        const { type } = this.props;
        const { isOpen } = this.state;
        let selectedText: string;

        if (type === ItemType.None)
            selectedText = ItemType[ItemType.None];
        else if (type === ItemType.All)
            selectedText = ItemType[ItemType.All];
        else if (TypeMultiSelect.isPowerOf2(type))
            selectedText = ItemType[type];
        else
            selectedText = "Mixed";

        return(
            <div className="relative" tabIndex={0} onClick={this.toggleOpen}>
                <div className="transition duration-150 ease-in-out px-2 py-1 text-sm flex items-center justify-between rounded-r bg-gray-600 text-gray-200 hover:shadow-lg hover:bg-gray-500 hover:text-gray-100 select-none cursor-pointer" style={{width: "10.29rem"}}>
                    <span>{selectedText}</span>
                    {(isOpen && 
                        <FontAwesomeIcon className="text-xs" icon="chevron-up" />
                    ) ||
                    (!isOpen && 
                        <FontAwesomeIcon className="text-xs" icon="chevron-down" />
                    )}
                </div>
                {(isOpen && 
                    <div className="absolute rounded bg-gray-600 text-gray-200" style={{top: "2rem", zIndex: 100}}>
                        {Object.keys(ItemType).filter(item => isNaN(parseInt(item))).map((value, index) => {
                            const itemType: ItemType = ItemType[value as keyof typeof ItemType];
                            
                            return(
                                <div key={index} className="flex flex-row items-center px-2 py-1 text-sm rounded hover:bg-gray-700 hover:text-gray-100 select-none cursor-pointer"
                                    tabIndex={0} 
                                    onClick={() => this.toggleType(itemType)}>
                                    {(this.checkType(itemType) && 
                                        <FontAwesomeIcon className="mr-2" icon="check" />
                                    ) || 
                                    (!this.checkType(itemType) && 
                                        <div className="" style={{width: "1.38rem"}}></div>
                                    )}
                                    {ItemType[itemType]}
                                </div>
                            );
                        })}
                    </div>
                )}
            </div>
        );
    }
}