import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import { ItemRarity } from "../utils/ItemRarity";

export type RarityMultiSelectProps = {
    rarity: ItemRarity;
    onChange(rarity: ItemRarity, callback?: (() => void) | undefined): void;
} 

export type RarityMultiSelectState = {
    isOpen: boolean;
}

export default class RarityMultiSelect extends React.Component<RarityMultiSelectProps, RarityMultiSelectState> {
    constructor(props: RarityMultiSelectProps) {
        super(props);

        this.state = {
            isOpen: false
        };

        this.toggleOpen = this.toggleOpen.bind(this);
        this.toggleRarity = this.toggleRarity.bind(this);
        this.checkRarity = this.checkRarity.bind(this);
    }

    private static isPowerOf2(rarity: ItemRarity): boolean {
        const rarityNum = rarity as number;
        return (rarityNum !== 0) && ((rarityNum & (rarityNum - 1)) === 0);
    }

    private toggleOpen() {
        const { isOpen } = this.state;
        if (isOpen)
            this.setState({isOpen: false});
        else
            this.setState({isOpen: true}, );
    }

    private toggleRarity(otherRarity: ItemRarity) {
        const { rarity, onChange } = this.props;
        let newRarity: ItemRarity;
        switch (otherRarity) {
        case ItemRarity.None:
        case ItemRarity.All:
            newRarity = otherRarity;
            break;
        default:
            newRarity = rarity ^ otherRarity;
            break;
        }

        onChange(newRarity, () => this.setState({isOpen: true}));
    }

    private checkRarity(otherRarity: ItemRarity): boolean {
        const { rarity } = this.props;
        if (rarity !== ItemRarity.All && otherRarity === ItemRarity.All)
            return false;
        return (rarity & otherRarity) !== 0;
    }
    
    render() {
        const { rarity } = this.props;
        const { isOpen } = this.state;
        let selectedText: string;

        if (rarity === ItemRarity.None)
            selectedText = ItemRarity[ItemRarity.None];
        else if (rarity === ItemRarity.All)
            selectedText = ItemRarity[ItemRarity.All];
        else if (RarityMultiSelect.isPowerOf2(rarity))
            selectedText = ItemRarity[rarity];
        else
            selectedText = "Mixed";

        return(
            <div className="relative" tabIndex={0} onClick={this.toggleOpen}>
                <div className="transition duration-150 ease-in-out px-2 py-1 flex items-center justify-between rounded-r text-sm bg-gray-600 text-gray-200 hover:shadow-lg hover:bg-gray-500 hover:text-gray-100 select-none cursor-pointer" style={{width: "6.9rem"}}>
                    <span>{selectedText}</span>
                    {(isOpen && 
                        <FontAwesomeIcon className="text-xs" icon="chevron-up" />
                    ) ||
                    (!isOpen && 
                        <FontAwesomeIcon className="text-xs" icon="chevron-down" />
                    )}
                </div>
                {(isOpen && 
                    <div className="absolute rounded bg-gray-600 text-gray-200" style={{top: "2rem", zIndex: 100}}>
                        {Object.keys(ItemRarity).filter(item => isNaN(parseInt(item))).map((value, index) => {
                            const itemRarity: ItemRarity = ItemRarity[value as keyof typeof ItemRarity];
                            
                            return(
                                <div key={index} className="flex flex-row items-center px-2 py-1 text-sm rounded hover:bg-gray-700 hover:text-gray-100 select-none cursor-pointer"
                                    tabIndex={0} 
                                    onClick={() => this.toggleRarity(itemRarity)}>
                                    {(this.checkRarity(itemRarity) && 
                                        <FontAwesomeIcon className="mr-2" icon="check" />
                                    ) || 
                                    (!this.checkRarity(itemRarity) && 
                                        <div className="" style={{width: "1.38rem"}}></div>
                                    )}
                                    {ItemRarity[itemRarity]}
                                </div>
                            );
                        })}
                    </div>
                )}
            </div>
        );
    }
}