import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import { Link } from "react-router-dom";
import { ItemList } from "../dataContracts/ItemList";
import environment from "../environment";
import ApiService from "../services/ApiService";
import CustomLink from "./CustomLink";

export type ItemListSlotProps = {
    itemList: ItemList;
    onDrop(id: string, itemId: number): Promise<void>;
    onEditName(id: string, name: string): Promise<void>;
    onDeleteList(id: string): Promise<void>;
}

export type ItemListSlotState = {
    isMouseHover: boolean;
    isDragHover: boolean;
    isEditing: boolean;
    newName: string;
}

export default class ItemListSlot extends React.Component<ItemListSlotProps, ItemListSlotState> {
    constructor(props: ItemListSlotProps) {
        super(props);

        this.state = {
            isMouseHover: false,
            isDragHover: false,
            isEditing: false,
            newName: props.itemList.name
        };

        this.onMouseEnter = this.onMouseEnter.bind(this);
        this.onMouseLeave = this.onMouseLeave.bind(this);
        this.onDragEnter = this.onDragEnter.bind(this);
        this.onDragOver = this.onDragOver.bind(this);
        this.onDragLeave = this.onDragLeave.bind(this);
        this.onDrop = this.onDrop.bind(this);

        this.onEditNameStart = this.onEditNameStart.bind(this);
        this.onEditNameStop = this.onEditNameStop.bind(this);
        this.onEditName = this.onEditName.bind(this);
        this.onDeleteList = this.onDeleteList.bind(this);
    }

    onMouseEnter() {
        this.setState({isMouseHover: true});
    }

    onMouseLeave() {
        this.setState({isMouseHover: false});
    }

    async onDragEnter(event: React.DragEvent<HTMLDivElement>) {
        event.preventDefault();
        this.setState({isDragHover: true});
    }

    onDragOver(event: React.DragEvent<HTMLDivElement>) {
        event.preventDefault();
    }

    async onDragLeave(event: React.DragEvent<HTMLDivElement>) {
        event.preventDefault();
        this.setState({isDragHover: false});
    }

    async onDrop(event: React.DragEvent<HTMLDivElement>) {
        event.preventDefault();
        const { itemList, onDrop } = this.props;
        const itemId = parseInt(event.dataTransfer.getData("text"));
        await onDrop(itemList.id, itemId);
        this.setState({isDragHover: false});
    }

    onEditNameStart(event: React.MouseEvent<HTMLButtonElement, MouseEvent>) {
        event.stopPropagation();
        this.setState({isEditing: true, newName: this.props.itemList.name});
    }

    onEditNameStop(event: React.MouseEvent<HTMLButtonElement, MouseEvent>) {
        event.stopPropagation();
        this.setState({isEditing: false});
    }

    async onEditName(event: React.MouseEvent<HTMLButtonElement, MouseEvent>) {
        event.stopPropagation();
        const { itemList, onEditName } = this.props;
        const { newName } = this.state;
        if (!newName)
            return;

        await onEditName(itemList.id, newName);
        this.setState({isEditing: false});
    }

    async onDeleteList(event: React.MouseEvent<HTMLButtonElement, MouseEvent>) {
        event.stopPropagation();
        const { itemList, onDeleteList } = this.props;
        await onDeleteList(itemList.id);
    }

    render() {
        const { itemList } = this.props;
        const { isMouseHover, isDragHover, isEditing, newName } = this.state;

        return(
            <CustomLink className={`transition duration-150 ease-in-out flex flex-row items-center justify-between px-4 py-3 mr-2 min-w-40 rounded xl:w-full xl:rounded-none xl:mr-0 xl:mb-2 bg-opacity-25 ${isDragHover ? "bg-green-700 text-green-300" : "bg-indigo-700 text-indigo-300"} hover:bg-opacity-100 hover:text-indigo-200 cursor-pointer`}
                to={`/list/${itemList.id}`}
                onMouseEnter={this.onMouseEnter}
                onMouseLeave={this.onMouseLeave}
                onDragEnter={this.onDragEnter}
                onDragOver={this.onDragOver}
                onDragLeave={this.onDragLeave}
                onDrop={this.onDrop}>
                {(!isEditing && 
                    <>
                        <span className="truncate pointer-events-none select-none">{itemList.name}</span>
                        <div className={`${isMouseHover ? "" : "hidden"} flex flex-row`}>
                            <button className="transition duration-150 ease-in-out px-2 mr-1 flex items-center rounded hover:shadow-lg bg-indigo-800 text-indigo-300 hover:bg-indigo-900 hover:text-indigo-200" style={{paddingTop: "0.35rem", paddingBottom: "0.35rem"}}
                                onClick={this.onEditNameStart}>
                                <FontAwesomeIcon className="text-xs" icon="pencil-alt" />
                            </button>
                            <button className="transition duration-150 ease-in-out px-2 flex items-center rounded hover:shadow-lg bg-indigo-800 text-indigo-300 hover:bg-indigo-900 hover:text-indigo-200"
                                onClick={this.onDeleteList}>
                                <FontAwesomeIcon className="text-xs" icon="trash-alt" />
                            </button>
                        </div>
                    </>
                ) || 
                (isEditing && 
                    <>
                        <input type="text" className="px-2 w-2/3 rounded bg-indigo-400 text-indigo-900" 
                            value={newName} 
                            onClick={event => event.stopPropagation()}
                            onChange={event => this.setState({newName: event.target.value})} />
                        <div className={`${isMouseHover ? "" : "hidden"} flex flex-row`}>
                            <button className="transition duration-150 ease-in-out px-2 mr-1 flex items-center rounded hover:shadow-lg bg-indigo-800 text-indigo-300 hover:bg-indigo-900 hover:text-indigo-200" style={{paddingTop: "0.35rem", paddingBottom: "0.35rem"}}
                                onClick={this.onEditName}>
                                <FontAwesomeIcon className="text-xs" icon="check" />
                            </button>
                            <button className="transition duration-150 ease-in-out px-2 flex items-center rounded hover:shadow-lg bg-indigo-800 text-indigo-300 hover:bg-indigo-900 hover:text-indigo-200"
                                onClick={this.onEditNameStop}>
                                <FontAwesomeIcon className="text-xs" icon="times" />
                            </button>
                        </div>
                    </>
                )}
            </CustomLink>
        );
    }
}