import { AuthenticationContext } from "@axa-fr/react-oidc-context";
import { User } from "oidc-client";
import React from "react";
import Scrollbar from "react-scrollbars-custom";
import ItemComparisonSlot from "../components/ItemComparisonSlot";
import ListSortingBar from "../components/ListSortingBar";
import { HistoryItemQuery } from "../dataContracts/HistoryItemQuery";
import { PricedItem } from "../dataContracts/PricedItem";
import ApiService from "../services/ApiService";
import LocalDB from "../services/LocalDB";
import { HistoryTimespan } from "../utils/HistoryTimespan";
import { HistoryItem } from "../dataContracts/HistoryItem";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export type MyListProps = {
    listId?: string;
}

export type MyListState = {
    id: string,
    user: User | undefined;
    items: PricedItem[];
    timespan: HistoryTimespan;
    isLoading: boolean;
    isDataStale: boolean;
}

export default class MyList extends React.Component<MyListProps, MyListState> {
    constructor(props: MyListProps) {
        super(props);

        this.state = {
            id: "",
            user: undefined,
            /*[19721, 46736, 46738, 46739, 72224, 9576, 24277, 67015],*/
            items: [],
            timespan: HistoryTimespan.OneMonth,
            isLoading: false,
            isDataStale: false
        };

        this.updateId = this.updateId.bind(this);
        this.updateItems = this.updateItems.bind(this);
        this.deleteItem = this.deleteItem.bind(this);
        this.onReload = this.onReload.bind(this);
        this.setTimespan = this.setTimespan.bind(this);
    }

    async componentDidMount() {
        await this.updateId();
    }

    async componentDidUpdate() {
        await this.updateId();
    }

    async updateId() {
        const { listId } = this.props;
        const { id } = this.state;
        if (listId && id !== listId)
            this.setState({id: listId, items: [], isLoading: true}, async () => {
                await this.updateItems();
            });
    }

    async updateItems() {
        const { user, id, timespan } = this.state;

        if (!user)
            return;

        this.setState({items: [], isLoading: true, isDataStale: false}, async () => {
            const ids = await ApiService.GetItemList(user.access_token, id);
            if (!ids)
                return;
    
            const historyItemQueries: HistoryItemQuery[] = [];
            for (var i = 0; i < ids.length; i++) {
                const itemId = ids[i];
                const newest = await LocalDB.getNewestTime(itemId);
                const hasDetails = await LocalDB.hasDetails(itemId);
                historyItemQueries.push(new HistoryItemQuery(itemId, hasDetails, newest));
            }
    
            const historyItems = await ApiService.GetHistoryItems(user.access_token, historyItemQueries);
            if (!historyItems)
                return;
    
            const pricedItems: PricedItem[] = [];
            for (var i = 0; i < historyItems.length; i++) {
                const item = historyItems[i];
                const itemQuery = historyItemQueries[i];
                await LocalDB.deleteOldPrices(item.Id);
                if (!itemQuery.hasDetails)
                    await LocalDB.setHistoryItem(item);
                else
                    await LocalDB.setHistoryItemPrices(item.Id, item.History);
    
                const dbItem = await LocalDB.getHistoryItem(item.Id, timespan);
                if (!dbItem)
                    continue;
    
                const pricedItem = MyList.historyToPriced(dbItem);
                pricedItems.push(pricedItem);
            }

            this.setState({items: pricedItems, isLoading: false}, () => {
                setTimeout(() => {
                    this.setState({isDataStale: true});
                }, 300000);
            });
        });
    }

    static historyToPriced(item: HistoryItem): PricedItem {
        const newestPrice = item.History[0];
        const buyPrice = newestPrice.buyPrice;
        const sellPrice = newestPrice.sellPrice;
        const margin = (sellPrice - buyPrice) / sellPrice * 100;
        let sumSold: number = 0;
        let sumBought: number = 0;
        item.History.forEach(price => {
            sumSold += price.sold;
            sumBought += price.bought;
        });

        const avgSold = sumSold / item.History.length;
        const avgBought = sumBought / item.History.length;

        const appeal = margin > 15 ? margin * avgSold * avgBought : 0;

        const pricedItem: PricedItem = {
            Id: item.Id,
            ChatLink: item.ChatLink,
            Name: item.Name,
            Icon: item.Icon,
            Description: item.Description,
            Type: item.Type,
            Rarity: item.Rarity,
            Level: item.Level,
        
            BuyPrice: buyPrice,
            SellPrice: sellPrice,
            Appeal: appeal,
            Margin: margin,
            Sold: avgSold,
            Bought: avgBought
        };

        return pricedItem;
    }

    async deleteItem(itemId: number) {
        const { id, user, items } = this.state;
        if (!user)
            return;

        const result = await ApiService.DeleteListItem(user.access_token, id, itemId);
        const tempItems = [...items];
        if (result) {
            const itemIndex = tempItems.findIndex(item => item.Id === itemId);
            tempItems.splice(itemIndex, 1);
            this.setState({items: tempItems});
        }
    }

    async onReload() {
        this.setState({items: [], isLoading: true}, async () => {
            await this.updateItems();
        });
    }

    async setTimespan(timespan: HistoryTimespan) {
        this.setState({items: [], timespan: timespan, isLoading: true}, async () => {
            const { user, id } = this.state;
            if (!user)
                return;

            const ids = await ApiService.GetItemList(user.access_token, id);
            if (!ids)
                return;

            const pricedItems: PricedItem[] = [];
            for (var i = 0; i < ids.length; i++) {
                const itemId = ids[i];
                const item = await LocalDB.getHistoryItem(itemId, timespan);
                if (!item)
                    continue;
                
                const pricedItem = MyList.historyToPriced(item);
                pricedItems.push(pricedItem);
            }

            this.setState({items: pricedItems, isLoading: false});
        });
    }

    render() {
        const { user, items, isLoading, isDataStale } = this.state;

        return (
            <div className="main w-full h-full px-4 py-4 bg-gray-800 text-gray-200">
                <AuthenticationContext.Consumer>
                    {props => {
                        if (props.oidcUser != null && !this.state.user)
                            this.setState({user: props.oidcUser});
                            
                        if (user) {
                            return(
                                <ListSortingBar items={items} name="Tracked Items" isDataStale={isDataStale} onReload={this.onReload} onSetTimespan={this.setTimespan}>
                                    {sortedItems => (
                                        <>
                                            {(sortedItems.length > 0 && 
                                                <Scrollbar noScrollX className="my-list-scroll">
                                                    {sortedItems.map((value, index) => (
                                                        <div key={index} className="mb-2">
                                                            <ItemComparisonSlot item={value} deleteItem={this.deleteItem} />
                                                        </div>
                                                    ))}
                                                </Scrollbar>
                                            ) || 
                                            (sortedItems.length <= 0 && !isLoading &&
                                                <div className="flex justify-center">
                                                    No items found.
                                                </div>
                                            ) || 
                                            (sortedItems.length <= 0 && isLoading &&
                                                <div className="flex flex-row items-center justify-center">
                                                    <FontAwesomeIcon icon="spinner" className="animate-spin mr-4 text-indigo-400" />
                                                    <span className="">Loading items...</span>
                                                </div>
                                            )}
                                        </>
                                    )}
                                </ListSortingBar>
                            );
                        }

                        return(<>
                            <ListSortingBar items={[]} name="Items" isDataStale={true} onReload={async () => {}} onSetTimespan={async () => {}}>
                                {sortedItems => (
                                    <></>
                                )}
                            </ListSortingBar>

                            {/*
                            <div className="">
                                Not logged in.
                            </div>
                            */}
                        </>);
                    }}
                </AuthenticationContext.Consumer>
                
                {/*
                <ItemComparisonSlot item={{ Id: 19721, Name: "Glob of Ectoplasm", Icon: "https://render.guildwars2.com/file/18CE5D78317265000CF3C23ED76AB3CEE86BA60E/65941.png", BuyPrice: 2022, SellPrice: 2232, Fee: Math.ceil(2232 * 0.15), AppealValue: 0, Margin: 0, TransactionRate: 0 }} />
                <div className="block">
                    <div id="head" className="">
                        <div className="flex flex-row flex-wrap">
                            <div className="font-bold">Name</div>
                            <div className="font-bold">Buys</div>
                        </div>
                    </div>
                    <div id="body" className="">

                    </div>
                </div>
                */}

                {/*
                <table className="table-fixed w-full">
                    <thead>
                        <tr>
                            <th className="w-2/8">Name</th>
                            <th className="w-1/8">Buys</th>
                            <th className="w-1/8">Sells</th>
                            <th className="w-1/8">Fee</th>
                            <th className="w-1/8">Appeal</th>
                            <th className="w-1/8">Margin</th>
                            <th className="w-1/8">Transaction Rate</th>
                        </tr>
                    </thead>
                    <tbody>
                        <ItemComparisonSlot item={{ Id: 19721, Name: "Glob of Ectoplasm", Icon: "https://render.guildwars2.com/file/18CE5D78317265000CF3C23ED76AB3CEE86BA60E/65941.png", BuyPrice: 2022, SellPrice: 2232, Fee: Math.ceil(2232 * 0.15), AppealValue: 0, Margin: 0, TransactionRate: 0 }} />
                    </tbody>
                </table>
                */}
            </div>
        );
    }
}