import { HistoryItem, HistoryItemFactory } from "../dataContracts/HistoryItem";
import { HistoryItemQuery } from "../dataContracts/HistoryItemQuery";
import { ItemList } from "../dataContracts/ItemList";
import { ItemQuery } from "../dataContracts/ItemQuery";
import { PricedItem, PricedItemFactory } from "../dataContracts/PricedItem";
import environment from "../environment";

class ApiService {
    public async GetItems(accessToken: string, query: ItemQuery, pageNumber: number): Promise<PricedItem[] | null> {
        const url = environment.urls.gw2calculator + "/item/pricedItems?" + new URLSearchParams(query.toObject()) + `&pageNumber=${pageNumber}`;
        const response = await ApiService.Get(url, accessToken);
        const result = await response.json();

        if (response.ok)
            return PricedItemFactory.ToPricedItems(result);

        console.log(result);
        return null;
    }

    public async GetItemPageCount(accessToken: string, query: ItemQuery): Promise<number> {
        const url = environment.urls.gw2calculator + "/item/pricedItems/pageCount?" + new URLSearchParams(query.toObject());
        const response = await ApiService.Get(url, accessToken);
        const result = await response.json();

        if (response.ok)
            return result;

        console.log(result);
        return 0;
    }

    public async GetHistoryItems(accessToken: string, queries: HistoryItemQuery[]): Promise<HistoryItem[] | null> {
        const url = environment.urls.gw2calculator + "/item/historyItems";
        const response = await ApiService.Post(url, accessToken, queries);
        const result = await response.json();

        if (response.ok)
            return HistoryItemFactory.ToHistoryItems(result);

        console.log(result);
        return null;
    }

    public async GetItemLists(accessToken: string) : Promise<ItemList[] | null> {
        const url = environment.urls.gw2calculator + "/list/getLists";
        const response = await ApiService.Get(url, accessToken);
        const result = await response.json();

        if (response.ok) {
            const items = result.itemLists;
            const itemLists: ItemList[] = [];
            items.forEach((item: any) => {
                const itemList: ItemList = {
                    id: item.id,
                    name: item.name
                };
                
                itemLists.push(itemList);
            });

            return itemLists;
        }

        console.log(result);
        return null;
    }

    public async GetItemList(accessToken: string, id: string) : Promise<number[] | null> {
        const url = environment.urls.gw2calculator + "/list/getList/" + id;
        const response = await ApiService.Get(url, accessToken);
        const result = await response.json();

        if (response.ok) {
            const items = result.itemIds;
            const itemIds: number[] = [];
            items.forEach((id: any) => {
                const idNum = parseInt(id);
                itemIds.push(idNum);
            });

            return itemIds;
        }

        console.log(result);
        return null;
    }

    public async CreateList(accessToken: string, name: string) : Promise<string | null> {
        const url = environment.urls.gw2calculator + "/list/createList";
        const response = await ApiService.Post(url, accessToken, {name});
        const result = await response.json();

        if (response.ok)
            return result.listId;

        console.log(result);
        return null;
    }

    public async UpdateListName(accessToken: string, id: string, name: string) : Promise<boolean> {
        const url = environment.urls.gw2calculator + "/list/updateName";
        const response = await ApiService.Post(url, accessToken, {id, name});

        if (response.ok)
            return true;

        const result = await response.json();
        console.log(result);
        return false;
    }

    public async AddListItem(accessToken: string, id: string, itemId: number) : Promise<boolean> {
        const url = environment.urls.gw2calculator + "/list/addItem";
        const response = await ApiService.Post(url, accessToken, {id, itemId});

        if (response.ok)
            return true;

        const result = await response.json();
        console.log(result);
        return false;
    }

    public async DeleteListItem(accessToken: string, id: string, itemId: number) : Promise<boolean> {
        const url = environment.urls.gw2calculator + "/list/deleteItem";
        const response = await ApiService.Post(url, accessToken, {id, itemId});

        if (response.ok)
            return true;
        
        const result = await response.json();
        console.log(result);
        return false;
    }

    public async DeleteList(accessToken: string, id: string) : Promise<boolean> {
        const url = environment.urls.gw2calculator + "/list/deleteList";
        const response = await ApiService.Post(url, accessToken, {id});

        if (response.ok)
            return true;

        const result = await response.json();
        console.log(result);
        return false;
    }

    private static async Get(url: string, accessToken: string) : Promise<Response> {
        return fetch(url, {
            method: "GET",
            mode: "cors",
            cache: "no-cache",
            headers: {
                "Content-Type": "application/json",
                "Authorization": `Bearer ${accessToken}`
            }
        });
    }

    private static async Post(url: string, accessToken: string, body: any) : Promise<Response> {
        return fetch(url, {
            method: "POST",
            mode: "cors",
            cache: "no-cache",
            headers: {
                "Content-Type": "application/json",
                "Authorization": `Bearer ${accessToken}`
            },
            body: body ? JSON.stringify(body) : undefined
        });
    }
}

export default new ApiService();