import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import Scrollbar from "react-scrollbars-custom";
import { PricedItem } from "../dataContracts/PricedItem";
import ItemHelper from "../utils/ItemHelper";
import Price from "./Price";

export type ItemSummarySlotProps = {
    item: PricedItem;
    accessToken: string;
}

export type ItemSummarySlotState = {
    isDragging: boolean;
    isAdding: boolean;
}

export default class ItemSummarySlot extends React.Component<ItemSummarySlotProps, ItemSummarySlotState> {
    private dragTarget: React.RefObject<HTMLDivElement>;
    
    constructor(props: ItemSummarySlotProps) {
        super(props);

        this.dragTarget = React.createRef();

        this.state = {
            isDragging: false,
            isAdding: false
        };

        this.onDragStart = this.onDragStart.bind(this);
        this.onDragEnd = this.onDragEnd.bind(this);
        this.onAdd = this.onAdd.bind(this);
    }

    onDragStart(event: React.DragEvent<HTMLDivElement>) {
        event.stopPropagation();
        event.dataTransfer.setData("text", this.props.item.Id.toString());

        requestAnimationFrame(() => {
            this.dragTarget.current?.classList.add("hide");
        });

        this.setState({isDragging: true});
    }

    onDragEnd(event: React.DragEvent<HTMLDivElement>) {
        this.dragTarget.current?.classList.remove("hide");
        this.setState({isDragging: false});
    }

    onAdd() {
        this.setState(prevState => ({isAdding: !prevState.isAdding}));
    }

    render() {
        const { item } = this.props;
        const { isDragging, isAdding } = this.state;

        return(
            <div className="flex flex-row px-4 py-2 bg-gray-700">
                <div className="flex flex-row w-3/12 items-center mr-6">
                    <img src={item.Icon} alt={item.Name} className="mr-4 w-12 h-12"/>
                    <a href={ItemHelper.getWikiLink(item.ChatLink)} target="_blank" rel="noopener noreferrer" 
                        className="transition duration-150 ease-in-out italic text-gray-200 hover:text-blue-400"
                        onClick={event => event.stopPropagation()}>
                        {item.Name}
                    </a>
                </div>
                <div className="flex flex-col w-2/12 items-center">
                    <label htmlFor="" className="text-sm mb-1 font-bold text-gray-500">Buys</label>
                    <Price price={item.BuyPrice} />
                </div>
                <div className="flex flex-col w-2/12 items-center">
                    <label htmlFor="" className="text-sm mb-1 font-bold text-gray-500">Sells</label>
                    <Price price={item.SellPrice} />
                </div>
                <div className="flex flex-col w-1/12 items-center">
                    <label htmlFor="" className="text-sm mb-1 font-bold text-gray-500">Margin</label>
                    <span className={`${item.Margin > 15 ? "text-green-500" : "text-red-500"}`}>{item.Margin}%</span>
                </div>
                <div className="flex flex-col w-1/12 items-center">
                    <label htmlFor="" className="text-sm mb-1 font-bold text-gray-500">Sold</label>
                    <span className="">{item.Sold}</span>
                </div>
                <div className="flex flex-col w-1/12 items-center">
                    <label htmlFor="" className="text-sm mb-1 font-bold text-gray-500">Bought</label>
                    <span className="">{item.Bought}</span>
                </div>
                <div className="flex flex-col w-1/12 items-center">
                    <label htmlFor="" className="text-sm mb-1 font-bold text-gray-500">Appeal</label>
                    <span className="">{item.Appeal}</span>
                </div>
                <div className="flex flex-row w-1/12 items-center justify-center">
                    <div className="relative px-2 py-1 mr-3 rounded shadow-lg bg-gray-800 text-gray-300 cursor-move"
                        draggable
                        onDragStart={this.onDragStart}
                        onDragEnd={this.onDragEnd}>
                        <FontAwesomeIcon icon="arrows-alt" />
                        <div ref={this.dragTarget} className={`${isDragging ? "" : "hidden"} absolute px-2 py-1 flex flex-row items-center justify-center w-32 h-12 rounded bg-gray-800`} style={{top: "-0.25rem", left: "-2.75rem"}}>
                            <img src={item.Icon} alt={item.Name} className="mr-3 w-8 h-8 select-none"/>
                            <span className="italic text-sm truncate text-gray-300 select-none">{item.Name}</span>
                        </div>
                    </div>
                    {/*
                    <div className="relative">
                        <button className="transition duration-150 ease-in-out px-3 py-2 rounded shadow-lg bg-green-700 text-green-300 hover:bg-green-800 hover:text-green-200"
                            onClick={this.onAdd}>
                            <FontAwesomeIcon icon="plus" />
                        </button>
                        <div className={`${isAdding ? "" : "hidden"} arrow-up absolute flex flex-col justify-center w-24 rounded bg-gray-900 text-gray-300`} style={{height: "5.45rem", top: "3.25rem", left: "-1.875rem"}}>
                            <Scrollbar noScrollX>
                                <div className="transition duration-150 ease-in-out px-2 py-1 text-sm rounded truncate hover:shadow-lg hover:bg-gray-600 select-none cursor-pointer"
                                    tabIndex={0}>
                                    Testasdfasdfasdf
                                </div>
                                <div className="transition duration-150 ease-in-out px-2 py-1 text-sm rounded truncate hover:shadow-lg hover:bg-gray-600 select-none cursor-pointer"
                                    tabIndex={0}>
                                    Testasdfasdfasdf
                                </div>
                                <div className="transition duration-150 ease-in-out px-2 py-1 text-sm rounded truncate hover:shadow-lg hover:bg-gray-600 select-none cursor-pointer"
                                    tabIndex={0}>
                                    Test
                                </div>
                                <div className="transition duration-150 ease-in-out px-2 py-1 text-sm rounded truncate hover:shadow-lg hover:bg-gray-600 select-none cursor-pointer"
                                    tabIndex={0}>
                                    Test
                                </div>
                            </Scrollbar>
                        </div>
                    </div>
                    */}
                </div>
            </div>
        );
    }
}