import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import { Link } from "react-router-dom";
import { ItemList } from "../dataContracts/ItemList";
import environment from "../environment";
import ApiService from "../services/ApiService";
import CustomLink from "./CustomLink";

export type AddItemListSlotProps = {
    onAddItemList(name: string): Promise<void>;
}

export type AddItemListSlotState = {
    isAdding: boolean;
    name: string;
}

export default class AddItemListSlot extends React.Component<AddItemListSlotProps, AddItemListSlotState> {
    constructor(props: AddItemListSlotProps) {
        super(props);

        this.state = {
            isAdding: false,
            name: ""
        };

        this.onAddItemListStart = this.onAddItemListStart.bind(this);
        this.onAddItemListStop = this.onAddItemListStop.bind(this);
        this.onAddItemList = this.onAddItemList.bind(this);
    }

    onAddItemListStart(event: React.MouseEvent<HTMLDivElement, MouseEvent>) {
        event.stopPropagation();
        this.setState({isAdding: true});
    }

    onAddItemListStop(event: React.MouseEvent<HTMLButtonElement, MouseEvent>) {
        event.stopPropagation();
        this.setState({isAdding: false});
    }

    async onAddItemList(event: React.MouseEvent<HTMLButtonElement, MouseEvent>) {
        event.stopPropagation();
        const { onAddItemList } = this.props;
        const { name } = this.state;
        if (!name)
            return;
        
        await onAddItemList(name);
        this.setState({isAdding: false});
    }

    render() {
        const { isAdding, name } = this.state;

        return(
            <div className="transition duration-150 ease-in-out flex flex-row items-center justify-between mr-2 xl:mx-2 border-2 border-dashed rounded-lg border-indigo-700 bg-opacity-0 bg-indigo-700 text-indigo-300 hover:bg-opacity-100 hover:text-indigo-200 cursor-pointer"
                tabIndex={0}
                onClick={this.onAddItemListStart}>
                {(!isAdding && 
                    <div className="px-4 py-3">
                        <div className="pointer-events-none select-none">
                            <FontAwesomeIcon icon="plus" className="mr-3" />
                            Add New List...
                        </div>
                    </div>
                ) || 
                (isAdding && 
                    <div className="px-2 py-3 flex flex-row">
                        <input type="text" className="px-2 mr-1 w-2/3 rounded bg-indigo-400 text-indigo-900" 
                            value={name} 
                            onClick={event => event.stopPropagation()}
                            onChange={event => this.setState({name: event.target.value})} />
                        <div className="flex flex-row">
                            <button className="transition duration-150 ease-in-out px-2 mr-1 flex items-center rounded hover:shadow-lg bg-indigo-800 text-indigo-300 hover:bg-indigo-900 hover:text-indigo-200" style={{paddingTop: "0.35rem", paddingBottom: "0.35rem"}}
                                onClick={this.onAddItemList}>
                                <FontAwesomeIcon className="text-xs" icon="check" />
                            </button>
                            <button className="transition duration-150 ease-in-out px-2 flex items-center rounded hover:shadow-lg bg-indigo-800 text-indigo-300 hover:bg-indigo-900 hover:text-indigo-200"
                                onClick={this.onAddItemListStop}>
                                <FontAwesomeIcon className="text-xs" icon="times" />
                            </button>
                        </div>
                    </div>
                )}
            </div>
        );
    }
}