import React from "react";
import logo from "./logo.svg";
import "./App.css";
import "./assets/main.css";
import MyList from "./pages/MyList";
import Dashboard from "./pages/Dashboard";
import GeneralSearch from "./pages/GeneralSearch";
import { BrowserRouter, Redirect, Route, Switch, useParams } from "react-router-dom";
import { AuthenticationProvider, withOidcSecure } from "@axa-fr/react-oidc-context";
import configuration from "./open-id-config";
import Header from "./components/Header";
import { config } from "process";
import Sidebar from "./components/Sidebar";

import { library } from "@fortawesome/fontawesome-svg-core";
import { fas } from "@fortawesome/free-solid-svg-icons";
import NotFound from "./pages/error/NotFound";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import NotAuthenticated from "./pages/error/NotAuthenticated";
import NotAuthorized from "./pages/error/NotAuthorized";

library.add(fas);

function App() {
    const afterLogin = () => (
        <div className="flex justify-center items-center w-screen h-screen bg-gray-700 text-gray-100">
            <div className="flex flex-row items-center">
                <FontAwesomeIcon icon="spinner" className="animate-spin mr-4 text-indigo-400" />
                <span className="">Logging in...</span>
            </div>
        </div>
    );

    const afterLogout = () => (
        <div className="flex justify-center items-center w-screen h-screen bg-gray-700 text-gray-100">
            <div className="flex flex-row items-center">
                <FontAwesomeIcon icon="spinner" className="animate-spin mr-4 text-indigo-400" />
                <span className="">Logging out...</span>
            </div>
        </div>
    );
    
    return(
        <div className="flex flex-col">
            <BrowserRouter>
                <AuthenticationProvider configuration={configuration}
                    notAuthenticated={NotAuthenticated}
                    notAuthorized={NotAuthorized}
                    callbackComponentOverride={afterLogin}
                    authenticating={afterLogout}>
                    <Header />
                    <Redirect path="/authentication/callback" to="/" />
                    <div className="flex flex-col xl:flex-row-reverse" style={{height: "calc(100vh - 4rem)"}}>
                        <Sidebar />
                        <Switch>
                            <Route exact path="/" component={Dashboard} />
                            <Route path="/search" component={GeneralSearch} />
                            <Route exact path="/list/:id" component={MyListComponent} />
                            <Route component={NotFound} />
                        </Switch>
                    </div>
                </AuthenticationProvider>
            </BrowserRouter>
        </div>
    );
}

function MyListComponent() {
    const { id } = useParams();

    return(
        <MyList listId={id} />
    );
}

export default App;
