import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";

export type PriceProps = {
    price: number;
    priceColor: string;
}

export type PriceState = {

}

export default class Price extends React.Component<PriceProps, PriceState> {

    static defaultProps: PriceProps = {
        price: 0,
        priceColor: "text-gray-200"
    }

    constructor(props: PriceProps) {
        super(props);

        this.state = {

        };
    }

    render() {
        const { price, priceColor } = this.props;
        const isNegative = price < 0;
        const absPrice = Math.abs(price);

        const gold = Math.floor(absPrice / 10000);
        const silver = Math.floor(absPrice % 10000 / 100);
        const copper = Math.floor(absPrice % 10000 % 100);

        return (
            <div className="flex flex-row">
                <div id="gold" className="mr-1 flex flex-row items-center">
                    <FontAwesomeIcon icon="minus" className={`${isNegative ? "" : "hidden"} ${priceColor} mr-1`} style={{fontSize: "0.5rem"}} />
                    <img src="https://wiki.guildwars2.com/images/d/d1/Gold_coin.png" alt="Gold Coin" className="mr-1 w-4 h-4"/>
                    <span className={`text-sm ${priceColor}`}>{gold}</span>
                </div>
                <div id="silver" className="mr-1 flex flex-row items-center">
                    <img src="https://wiki.guildwars2.com/images/3/3c/Silver_coin.png" alt="Silver Coin" className="mr-1 w-4 h-4"/>
                    <span className={`text-sm ${priceColor}`}>{silver}</span>
                </div>
                <div id="copper" className="mr-1 flex flex-row items-center">
                    <img src="https://wiki.guildwars2.com/images/e/eb/Copper_coin.png" alt="Copper Coin" className="mr-1 w-4 h-4"/>
                    <span className={`text-sm ${priceColor}`}>{copper}</span>
                </div>
            </div>
        );
    }
}