import { AuthenticationContext } from "@axa-fr/react-oidc-context";
import React from "react";
import { Link } from "react-router-dom";

export default class Header extends React.Component {
    render() {
        return(
            <header className="" style={{boxShadow: "0 10px 15px -3px rgba(0, 0, 0, 0.2)", zIndex: 100}}>
                <AuthenticationContext.Consumer>
                    {props => {
                        return(
                            <div className="flex justify-between items-center px-4 bg-indigo-700 text-indigo-200">
                                <div className="flex flex-row items-center">
                                    <Link className="transition duration-150 ease-in-out mr-6 text-lg font-bold hover:text-indigo-100"
                                        to="/">
                                        GW2 Trading
                                    </Link>
                                    <Link className="transition duration-150 ease-in-out mr-3 py-5 px-2 hover:bg-indigo-600 hover:text-indigo-100"
                                        to="/">
                                        Dashboard
                                    </Link>
                                    <Link className="transition duration-150 ease-in-out mr-3 py-5 px-2 hover:bg-indigo-600 hover:text-indigo-100"
                                        to="/search">
                                        Search
                                    </Link>
                                </div>

                                {props.oidcUser && (
                                    <div className="">
                                        <span className="mr-4">Hello, {props.oidcUser.profile.name}!</span>
                                        <button className="transition duration-150 ease-in-out px-4 py-1 bg-indigo-800 hover:shadow-lg hover:bg-indigo-900 hover:text-indigo-100"
                                            onClick={() => props.logout()}>
                                            Logout
                                        </button>
                                    </div>
                                ) ||
                                (!props.oidcUser && (
                                    <div className="">
                                        <button className="transition duration-150 ease-in-out px-4 py-1 bg-indigo-800 hover:shadow-lg hover:bg-indigo-900 hover:text-indigo-100"
                                            onClick={() => props.login()}>
                                            Login
                                        </button>
                                    </div>
                                ))}
                            </div>
                        );
                    }}
                </AuthenticationContext.Consumer>
            </header>
        );
    }
}