import { AuthenticationContext } from "@axa-fr/react-oidc-context";
import { faDivide } from "@fortawesome/free-solid-svg-icons";
import { User } from "oidc-client";
import React from "react";
import Scrollbar from "react-scrollbars-custom";
import ItemSummarySlot from "../components/ItemSummarySlot";
import SortingBar from "../components/SortingBar";
import { PricedItem } from "../dataContracts/PricedItem";
import { ItemRarity } from "../utils/ItemRarity";
import { ItemType } from "../utils/ItemType";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export type GeneralSearchProps = {

}

export type GeneralSearchState = {
    user: User | undefined;
    isLoading: boolean;
}

export default class GeneralSearch extends React.Component<GeneralSearchProps, GeneralSearchState> {
    constructor(props: GeneralSearchProps) {
        super(props);

        this.state = {
            user: undefined,
            isLoading: false
        };

        this.onLoadingStart = this.onLoadingStart.bind(this);
        this.onLoadingStop = this.onLoadingStop.bind(this);
    }

    onLoadingStart() {
        this.setState({isLoading: true});
    }

    onLoadingStop() {
        this.setState({isLoading: false});
    }

    render() {
        const { user, isLoading } = this.state;

        return(
            <div className="main w-full h-full px-4 py-4 bg-gray-800 text-gray-200">
                <AuthenticationContext.Consumer>
                    {props => {
                        if (props.oidcUser != null && !this.state.user)
                            this.setState({user: props.oidcUser});

                        if (user) {
                            return(
                                <SortingBar name="Results" onLoadingStart={this.onLoadingStart} onLoadingStop={this.onLoadingStop}>
                                    {sortedItems => (
                                        <>
                                            {(sortedItems.length > 0 && 
                                                <Scrollbar noScrollX className="search-scroll">
                                                    {sortedItems.map((value, index) => (
                                                        <div key={index} className="mb-2">
                                                            <ItemSummarySlot item={value} accessToken={user.access_token} />
                                                        </div>
                                                    ))}
                                                </Scrollbar>
                                            ) || 
                                            (sortedItems.length <= 0 && !isLoading &&
                                                <div className="">
                                                    No items found.
                                                </div>
                                            ) || 
                                            (sortedItems.length <= 0 && isLoading &&
                                                <div className="flex flex-row items-center justify-center">
                                                    <FontAwesomeIcon icon="spinner" className="animate-spin mr-4 text-indigo-400" />
                                                    <span className="">Loading items...</span>
                                                </div>
                                            )}
                                        </>
                                    )}
                                </SortingBar>
                            );
                        }

                        return(
                            <div className="">
                                Not logged in.
                            </div>
                        );
                    }}
                </AuthenticationContext.Consumer>
            </div>
        );
    }
}