import { AreaPoint, AreaSeriesTypes, AutoCursorModes, AxisScrollStrategies, AxisTickStrategies, ChartOptions, ChartXY, ColorRGBA, Dashboard, emptyLine, InternalBackground, LegendBox, LegendBoxBuilders, LegendBoxEntry, lightningChart, LineStyle, Point, PointableBackground, PointMarker, SolidFill, SolidLine, Themes, UIBackground, UIElement, UIOrigins, UIPartWithBackground, UITextBox, XOHLC } from "@arction/lcjs";
import { createOHLCGenerator } from "@arction/xydata";
import React from "react";
import { ItemPriceSnapshot } from "../dataContracts/ItemPriceSnapshot";
import LocalDB from "../services/LocalDB";
import { HistoryTimespan } from "../utils/HistoryTimespan";

export type ItemGraphProps = {
    itemId: number;
}

export type ItemGraphState = {

}

export default class ItemGraph extends React.Component<ItemGraphProps, ItemGraphState> {
    private dashboard: Dashboard | undefined;
    private buyPriceChart: ChartXY<PointMarker, UIBackground, PointableBackground, PointableBackground> | undefined;
    private sellPriceChart: ChartXY<PointMarker, UIBackground, PointableBackground, PointableBackground> | undefined;
    private marginChart: ChartXY<PointMarker, UIBackground, PointableBackground, PointableBackground> | undefined;
    private buyVolumeChart: ChartXY<PointMarker, UIBackground, PointableBackground, PointableBackground> | undefined;
    private sellVolumeChart: ChartXY<PointMarker, UIBackground, PointableBackground, PointableBackground> | undefined;

    constructor(props: ItemGraphProps) {
        super(props);

        this.state = {

        };

        this.createChart = this.createChart.bind(this);
        this.createOHLCChart = this.createOHLCChart.bind(this);
    }

    async createChart() {
        const { itemId } = this.props;
        // Get the history item from the local db here
        /* */
        const item = await LocalDB.getHistoryItem(itemId, HistoryTimespan.OneMonth);
        if (!item)
            return;

        // TODO: Replace this big number with the time of the earliest price snapshot in history
        const timeSpanInMs = 15 * 60 * 1000; // 15 mins
        const firstPriceSnapshot = item.History[item.History.length - 1];
        const startingTime = firstPriceSnapshot.time - (firstPriceSnapshot.time % timeSpanInMs);
        const buyPricesData: XOHLC[] = [];
        const sellPricesData: XOHLC[] = [];
        const actualBuyPricesData: Point[] = [];
        const actualSellPricesData: Point[] = [];
        const marginData: Point[] = [];
        const buyVolumeData: AreaPoint[] = [];
        const sellVolumeData: AreaPoint[] = [];

        let pricesIn15Mins: ItemPriceSnapshot[] = [];
        let timeTracker = 0;
        // Go backwards here because the history is sorted in reverse
        for (var i = item.History.length - 1; i >= 0; i--) {
            const priceSnapshot = item.History[i];
            const time = timeTracker + startingTime;
            const timeFromStart = priceSnapshot.time - startingTime;
            const margin = ((priceSnapshot.sellPrice - priceSnapshot.buyPrice) / priceSnapshot.sellPrice) * 100;

            actualBuyPricesData.push({x: timeFromStart, y: priceSnapshot.buyPrice});
            actualSellPricesData.push({x: timeFromStart, y: priceSnapshot.sellPrice});
            marginData.push({x: timeFromStart, y: margin});
            buyVolumeData.push({position: priceSnapshot.time - startingTime, high: priceSnapshot.bought, low: priceSnapshot.bid * -1});
            sellVolumeData.push({position: priceSnapshot.time - startingTime, high: priceSnapshot.sold, low: priceSnapshot.offers * -1});

            if (priceSnapshot.time < time + timeSpanInMs && priceSnapshot.time >= time)
                pricesIn15Mins.push(priceSnapshot);

            // This is for checking if there's no next price snapshot
            const nextPriceSnapshot = i - 1 >= 0 ? item.History[i - 1] : undefined;
            
            // This means the next price snapshot is already beyond the time interval we're compiling
            if (!nextPriceSnapshot || nextPriceSnapshot.time >= time + timeSpanInMs) {
                timeTracker += timeSpanInMs;
                if (pricesIn15Mins.length <= 0)
                    continue;

                let minBuy = Number.POSITIVE_INFINITY;
                let maxBuy = 0;
                let minSell = Number.POSITIVE_INFINITY;
                let maxSell = 0;

                for (var j = 0; j < pricesIn15Mins.length; j++) {
                    const price = pricesIn15Mins[j];
                    if (price.buyPrice < minBuy)
                        minBuy = price.buyPrice;
                    if (price.buyPrice > maxBuy)
                        maxBuy = price.buyPrice;

                    if (price.sellPrice < minSell)
                        minSell = price.sellPrice;
                    if (price.sellPrice > maxSell)
                        maxSell = price.sellPrice;
                }

                const firstSnapshot = pricesIn15Mins[0];
                const lastSnapshot = pricesIn15Mins[pricesIn15Mins.length - 1];
                const buyDataPoint: XOHLC = [timeTracker, firstSnapshot.buyPrice, maxBuy, minBuy, lastSnapshot.buyPrice];
                const sellDataPoint: XOHLC = [timeTracker, firstSnapshot.sellPrice, maxSell, minSell, lastSnapshot.sellPrice];

                buyPricesData.push(buyDataPoint);
                sellPricesData.push(sellDataPoint);

                minBuy = Number.POSITIVE_INFINITY;
                maxBuy = 0;
                minSell = Number.POSITIVE_INFINITY;
                maxSell = 0;
                pricesIn15Mins = [];
            }
        }
        /**/

        // Define all chart options here
        this.dashboard = lightningChart().Dashboard({
            theme: Themes.dark,
            numberOfColumns: 2,
            numberOfRows: 3,
            container: itemId.toString()
        });

        this.buyPriceChart = this.createOHLCChart("Buy Prices", startingTime, true, buyPricesData, actualBuyPricesData, {
            columnIndex: 0,
            rowIndex: 0,
            columnSpan: 1,
            rowSpan: 1
        });
        this.sellPriceChart = this.createOHLCChart("Sell Prices", startingTime, false, sellPricesData, actualSellPricesData, {
            columnIndex: 1,
            rowIndex: 0,
            columnSpan: 1,
            rowSpan: 1
        });

        this.marginChart = this.createBaselineAreaChart("Margin", "Margin", 15, startingTime, marginData, {
            columnIndex: 0,
            rowIndex: 1,
            columnSpan: 2,
            rowSpan: 1
        });

        this.buyVolumeChart = this.createAreaChart("Buy Volume \ud835\udeab", "Bought", "Bid", startingTime, buyVolumeData, {
            columnIndex: 0,
            rowIndex: 2,
            columnSpan: 1,
            rowSpan: 1
        });
        this.sellVolumeChart = this.createAreaChart("Sell Volume \ud835\udeab", "Sold", "Offers", startingTime, sellVolumeData, {
            columnIndex: 1,
            rowIndex: 2,
            columnSpan: 1,
            rowSpan: 1
        });
    }

    createOHLCChart(title: string, startingTime: number, invertCandleColors: boolean, data: XOHLC[], priceData: Point[], options: ChartOptions<PointMarker, UIBackground, PointableBackground, PointableBackground>) {
        if (!this.dashboard)
            return;

        //#region Buy Price Chart
        const chart = this.dashboard.createChartXY(options);

        // TODO: Make this the date and time of the very first price snapshot for an item
        const dateOrigin = new Date(startingTime); 
        chart.getDefaultAxisX()
            .setTickStrategy(
                AxisTickStrategies.DateTime,
                (tickStrategy) => tickStrategy.setDateOrigin(dateOrigin)
            );

        chart.setTitle(title)
            .setAutoCursor(cursor => {
                cursor.disposeTickMarkerY();
                cursor.setGridStrokeYStyle(emptyLine);
            })
            .setPadding({right: 40});

        const priceLegend = chart
            .addLegendBox(LegendBoxBuilders.VerticalLegendBox)
            .setPosition({x: 100, y: 100})
            .setOrigin(UIOrigins.RightTop);

        const priceAxisY = chart
            .getDefaultAxisY()
            .setScrollStrategy(AxisScrollStrategies.expansion)
            .setTitle("Price");

        // Colors of the Bollinger Band
        const areaRangeFill = new SolidFill().setColor(ColorRGBA(100, 149, 237, 50));
        const areaRangeStroke = new SolidLine()
            .setFillStyle(new SolidFill().setColor(ColorRGBA(100, 149, 237)))
            .setThickness(1);

        // Settings of the Bollinger Band
        const areaRange = chart.addAreaRangeSeries({ yAxis: priceAxisY })
            .setName("Bollinger Band")
            .setHighFillStyle(areaRangeFill)
            .setLowFillStyle(areaRangeFill)
            .setHighStrokeStyle(areaRangeStroke)
            .setLowStrokeStyle(areaRangeStroke)
            .setMaxPointCount(0) // Solves the part of the area vanishing when zooming in
            .setMouseInteractions(false)
            .setCursorEnabled(false);

        const movingAverageStroke = new SolidLine()
            .setFillStyle(new SolidFill().setColor(ColorRGBA(236, 201, 75)))
            .setThickness(2);
        const movingAverage = chart.addLineSeries()
            .setName("Moving Average")
            .setStrokeStyle(movingAverageStroke)
            .setMouseInteractions(false)
            .setCursorEnabled(false);

        const pricesLineStroke = new SolidLine()
            .setFillStyle(new SolidFill().setColor(ColorRGBA(237, 137, 54)))
            .setThickness(2);
        const pricesLine = chart.addLineSeries()
            .setName("Actual Prices")
            .setStrokeStyle(pricesLineStroke)
            .setCursorInterpolationEnabled(false);

        const priceFigureWidth = 5.0;
        const borderBlack = new SolidLine()
            .setFillStyle(new SolidFill().setColor(ColorRGBA(26, 32, 44)))
            .setThickness(1);
        const fillBrightRed = new SolidFill().setColor(ColorRGBA(229, 62, 62));
        const fillDimRed = new SolidFill().setColor(ColorRGBA(116, 42, 42));
        const fillBrightGreen = new SolidFill().setColor(ColorRGBA(62, 225, 62));
        const fillDimGreen = new SolidFill().setColor(ColorRGBA(34, 84, 61));

        const candles = chart
            .addOHLCSeries({yAxis: priceAxisY});

        candles.setName("Candlesticks")
            .setFigureWidth(priceFigureWidth)
            .setPositiveStyle(candlestick => 
                candlestick
                    .setBodyFillStyle(invertCandleColors ? fillBrightRed : fillBrightGreen)
                    .setBodyStrokeStyle(borderBlack)
                    .setStrokeStyle(new SolidLine()
                        .setFillStyle(invertCandleColors ? fillDimRed : fillDimGreen)
                        .setThickness(2))
            )
            .setNegativeStyle(candlestick => 
                candlestick
                    .setBodyFillStyle(invertCandleColors ? fillBrightGreen : fillBrightRed)
                    .setBodyStrokeStyle(borderBlack)
                    .setStrokeStyle(new SolidLine()
                        .setFillStyle(invertCandleColors ? fillDimGreen : fillDimRed)
                        .setThickness(2))
            );

        const priceNums: number[] = [];
        let n: number = 0;
        const period = 30;
        const add: (ohlc: XOHLC) => void = (ohlc) => {
            candles.add(ohlc);

            priceNums.push((ohlc[2] + ohlc[3] + ohlc[4]) / 3);

            if (n < period) {
                n++;
            } else {
                priceNums.shift();
            }

            let priceSum = 0;
            priceNums.forEach(num => priceSum += num);
            const priceAverage = priceSum / n;

            let sqrDeviationSum = 0;
            priceNums.forEach(num => sqrDeviationSum += Math.pow(num - priceAverage, 2));
            const stdDeviation = Math.sqrt(sqrDeviationSum / n);
            
            const areaPoint: AreaPoint = {
                position: ohlc[0],
                high: priceAverage + (stdDeviation * 2),
                low: priceAverage - (stdDeviation * 2)
            };
            areaRange.add(areaPoint);

            const smaPoint: Point = {
                x: ohlc[0],
                y: priceAverage
            };
            movingAverage.add(smaPoint);
        };

        /*
        createOHLCGenerator()
            .setNumberOfPoints(100)
            .setDataFrequency(15 * 60 * 1000)
            .generate()
            .toPromise()
            .then(data => {
                data.forEach(add);
                setViewNicely();
            });
        /**/
        //#endregion

        const entries: LegendBoxEntry[] = priceLegend.add(chart) as LegendBoxEntry[];
        entries[0]
            .setButtonOnFillStyle(areaRangeStroke.getFillStyle())
            .setButtonOnStrokeStyle(emptyLine);

        entries[1]
            .setOn(false);
        entries[2]
            .setOn(false);

        const setViewNicely = () => {
            const yBoundsPrice = { min: (areaRange.getYMin() ?? 0), max: (areaRange.getYMax() ?? 0), range: (areaRange.getYMax() ?? 0) - (areaRange.getYMin() ?? 0) };
            // Set Y axis intervals so that series don't overlap and volume is under stocks.
            priceAxisY.setInterval(yBoundsPrice.min - yBoundsPrice.range * .33, yBoundsPrice.max);
        };

        // For the candlesticks on hover tooltip
        candles.setResultTableFormatter((builder, series, segment) => {
            const open = segment.getOpen();
            const close = segment.getClose();
            return builder
                .addRow(series.getName())
                .addRow(series.axisX.formatValue(segment.getPosition()))
                .addRow(`High: ${ItemGraph.toPriceString(segment.getHigh())}`)
                .addRow(`${open > close ? `Open: ${ItemGraph.toPriceString(open)}` : `Close: ${ItemGraph.toPriceString(close)}`}`)
                .addRow(`${open < close ? `Open: ${ItemGraph.toPriceString(open)}` : `Close: ${ItemGraph.toPriceString(close)}`}`)
                .addRow(`Low: ${ItemGraph.toPriceString(segment.getLow())}`);
        });

        pricesLine.setResultTableFormatter((builder, series, x, y) => {
            return builder
                .addRow(series.getName())
                .addRow(series.axisX.formatValue(x))
                .addRow(`Price: ${ItemGraph.toPriceString(y)}`);
        });

        /* */
        data.forEach(add);
        pricesLine.add(priceData);
        setViewNicely();
        /**/

        return chart;
    }

    createAreaChart(title: string, topTitle: string, bottomTitle: string, startingTime: number, data: AreaPoint[], options: ChartOptions<PointMarker, UIBackground, PointableBackground, PointableBackground>) {
        if (!this.dashboard)
            return;

        const chart = this.dashboard.createChartXY(options);

        const dateOrigin = new Date(startingTime);
        chart.getDefaultAxisX()
            .setTickStrategy(
                AxisTickStrategies.DateTime,
                tickStrategy => tickStrategy.setDateOrigin(dateOrigin)
            );

        chart.setTitle(title)
            .setPadding({right: 40});

        const volumeLegend = chart
            .addLegendBox(LegendBoxBuilders.VerticalLegendBox)
            .setPosition({x: 100, y: 100})
            .setOrigin(UIOrigins.RightTop);

        const volumeAxisY = chart
            .getDefaultAxisY()
            .setScrollStrategy(AxisScrollStrategies.expansion)
            .setTitle("Volume");

        
        const topAreaFill = new SolidFill().setColor(ColorRGBA(72, 187, 120, 50));
        const topAreaStroke = new SolidLine()
            .setFillStyle(new SolidFill().setColor(ColorRGBA(72, 187, 120)))
            .setThickness(1);
        
        const topArea = chart.addAreaSeries({ type: AreaSeriesTypes.Positive, yAxis: volumeAxisY })
            .setName(topTitle)
            .setFillStyle(topAreaFill)
            .setStrokeStyle(topAreaStroke)
            .setCursorInterpolationEnabled(false)
            .setMaxPointCount(0); // Solves the part of the area vanishing when zooming in
            
        const bottomAreaFill = new SolidFill().setColor(ColorRGBA(245, 101, 101, 50));
        const bottomAreaStroke = new SolidLine()
            .setFillStyle(new SolidFill().setColor(ColorRGBA(245, 101, 101)))
            .setThickness(1);
        
        const bottomArea = chart.addAreaSeries({ type: AreaSeriesTypes.Negative, yAxis: volumeAxisY })
            .setName(bottomTitle)
            .setFillStyle(bottomAreaFill)
            .setStrokeStyle(bottomAreaStroke)
            .setCursorInterpolationEnabled(false)
            .setMaxPointCount(0); // Solves the part of the area vanishing when zooming in
        
        topArea.setResultTableFormatter((builder, series, position, high) => {
            return builder
                .addRow(topTitle)
                .addRow(series.axisX.formatValue(position))
                .addRow(`Amount: ${high.toFixed(0)}`);
        });
        
        bottomArea.setResultTableFormatter((builder, series, position, high) => {
            return builder
                .addRow(bottomTitle)
                .addRow(series.axisX.formatValue(position))
                .addRow(`Amount: ${(high * -1).toFixed(0)}`);
        });

        volumeLegend.add(chart);

        data.forEach(dataPoint => {
            topArea.add({x: dataPoint.position, y: dataPoint.high});
            bottomArea.add({x: dataPoint.position, y: dataPoint.low});
        });

        return chart;
    }

    createBaselineAreaChart(title: string, yTitle: string, baseline: number, startingTime: number, data: Point[], options: ChartOptions<PointMarker, UIBackground, PointableBackground, PointableBackground>) {
        if (!this.dashboard)
            return;

        const chart = this.dashboard.createChartXY(options);

        const dateOrigin = new Date(startingTime);
        chart.getDefaultAxisX()
            .setTickStrategy(
                AxisTickStrategies.DateTime,
                tickStrategy => tickStrategy.setDateOrigin(dateOrigin)
            );

        chart.setTitle(title)
            .setPadding({right: 40});

        const lineAxisY = chart
            .getDefaultAxisY()
            .setScrollStrategy(AxisScrollStrategies.expansion)
            .setTitle(yTitle);

        
        const topAreaFill = new SolidFill().setColor(ColorRGBA(72, 187, 120, 50));
        const topAreaStroke = new SolidLine()
            .setFillStyle(new SolidFill().setColor(ColorRGBA(72, 187, 120)))
            .setThickness(1);
        
        const bottomAreaFill = new SolidFill().setColor(ColorRGBA(245, 101, 101, 50));
        const bottomAreaStroke = new SolidLine()
            .setFillStyle(new SolidFill().setColor(ColorRGBA(245, 101, 101)))
            .setThickness(1);

        const area = chart.addAreaSeries({baseline: baseline, type: AreaSeriesTypes.Bipolar, yAxis: lineAxisY})
            .setName(title)
            .setPositiveFillStyle(topAreaFill)
            .setPositiveStrokeStyle(topAreaStroke)
            .setNegativeFillStyle(bottomAreaFill)
            .setNegativeStrokeStyle(bottomAreaStroke)
            .setCursorInterpolationEnabled(false);

        area.setResultTableFormatter((builder, series, x, y) => {
            return builder
                .addRow(series.getName())
                .addRow(series.axisX.formatValue(x))
                .addRow(`${yTitle}: ${y.toFixed(2)}%`);
        });

        area.add(data);

        return chart;
    }

    static toPriceString(price: number) {
        const gold = Math.floor(price / 10000);
        const silver = Math.floor(price % 10000 / 100);
        const copper = Math.floor(price % 10000 % 100);

        return `${gold}g ${silver}s ${copper}c`;
    }

    async componentDidMount() {
        await this.createChart();
    }

    componentWillUnmount() {
        this.sellVolumeChart?.dispose();
        this.buyVolumeChart?.dispose();
        this.marginChart?.dispose();
        this.sellPriceChart?.dispose();
        this.buyPriceChart?.dispose();
        this.dashboard?.dispose();

        this.sellVolumeChart = undefined;
        this.buyVolumeChart = undefined;
        this.marginChart = undefined;
        this.sellPriceChart = undefined;
        this.buyPriceChart = undefined;
        this.dashboard = undefined;
    }

    render() {
        const { itemId } = this.props;

        return(
            <div id={itemId.toString()} className="h-full"></div>
        );
    }
}