import { AuthenticationContext } from "@axa-fr/react-oidc-context";
import { User } from "oidc-client";
import React from "react";
import Scrollbar from "react-scrollbars-custom";
import { ItemList } from "../dataContracts/ItemList";
import ApiService from "../services/ApiService";
import AddItemListSlot from "./AddItemListSlot";
import ItemListSlot from "./ItemListSlot";

export type SidebarProps = {

}

export type SidebarState = {
    user: User | undefined;
    itemLists: ItemList[];
}

export default class Sidebar extends React.Component<SidebarProps, SidebarState> {
    constructor(props: SidebarProps) {
        super(props);

        this.state = {
            user: undefined,
            itemLists: []
        };

        this.refreshItemLists = this.refreshItemLists.bind(this);

        this.onAddItem = this.onAddItem.bind(this);
        this.onEditName = this.onEditName.bind(this);
        this.onAddList = this.onAddList.bind(this);
        this.onDeleteList = this.onDeleteList.bind(this);
    }

    async refreshItemLists() {
        const { user } = this.state;
        if (!user)
            return;

        const itemLists = await ApiService.GetItemLists(user.access_token);
        if (!itemLists)
            return;

        this.setState({itemLists});
    }

    async onAddItem(id: string, itemId: number) {
        const { user } = this.state;
        if (!user)
            return;

        await ApiService.AddListItem(user.access_token, id, itemId);
    }

    async onEditName(id: string, name: string) {
        const { user, itemLists } = this.state;
        if (!user)
            return;

        const result = await ApiService.UpdateListName(user.access_token, id, name);
        if (result) {
            const index = itemLists.findIndex(list => list.id === id);
            itemLists[index].name = name;
            this.setState({itemLists});
        }
    }

    async onAddList(name: string) {
        const { user, itemLists } = this.state;
        if (!user)
            return;

        const result = await ApiService.CreateList(user.access_token, name);
        if (result) {
            const itemList: ItemList = {
                id: result,
                name: name
            };
            itemLists.push(itemList);
            this.setState({itemLists});
        }
    }

    async onDeleteList(id: string) {
        const { user, itemLists } = this.state;
        if (!user)
            return;

        const result = await ApiService.DeleteList(user.access_token, id);
        if (result) {
            const index = itemLists.findIndex(list => list.id === id);
            itemLists.splice(index, 1);
            this.setState({itemLists});
        }
    }

    render() {
        const { itemLists, user } = this.state;

        return(
            <div className="sidebar py-4 flex w-full h-24 xl:w-64 xl:h-full bg-gray-900">
                <AuthenticationContext.Consumer>
                    {props => {
                        if (props.oidcUser != null && !this.state.user)
                            this.setState({user: props.oidcUser}, () => {
                                this.refreshItemLists();
                            });
                        if (user) {
                            return(
                                <Scrollbar>
                                    <div className="flex flex-row px-4 xl:px-0 xl:flex-col whitespace-no-wrap">
                                        {itemLists.map((itemList, index) => (
                                            <React.Fragment key={index}>
                                                <ItemListSlot itemList={itemList} onDrop={this.onAddItem} onEditName={this.onEditName} onDeleteList={this.onDeleteList} />
                                            </React.Fragment>
                                        ))}
                                        <AddItemListSlot onAddItemList={this.onAddList} />
                                    </div>
                                </Scrollbar>
                            );
                        }

                        return(<></>);
                    }}
                </AuthenticationContext.Consumer>
            </div>
        );
    }
}