import { ItemRarity } from "../utils/ItemRarity";
import { ItemType } from "../utils/ItemType";
import { ItemPriceSnapshot } from "./ItemPriceSnapshot";

export class HistoryItemPrices {
    public readonly Id: number;
    public readonly History: ItemPriceSnapshot[];

    constructor(id: number, history: ItemPriceSnapshot[]) {
        this.Id = id;
        this.History = history;
    }
}

export class HistoryItem {
    public readonly Id: number;
    public readonly ChatLink: string;
    public readonly Name: string;
    public readonly Icon: string;
    public readonly Description: string;
    public readonly Type: ItemType;
    public readonly Rarity: ItemRarity;
    public readonly Level: number;
    public History: ItemPriceSnapshot[];

    constructor(id: number, chatLink: string, name: string, icon: string, description: string, type: ItemType, rarity: ItemRarity, level: number, history: ItemPriceSnapshot[]) {
        this.Id = id;
        this.ChatLink = chatLink;
        this.Name = name;
        this.Icon = icon;
        this.Description = description;
        this.Type = type;
        this.Rarity = rarity;
        this.Level = level;
        this.History = history;
    }

    filterHistory(predicate: (value: ItemPriceSnapshot, index: number, array: ItemPriceSnapshot[]) => unknown) {
        this.History = this.History.filter(predicate);
    }
}

export class HistoryItemFactory {
    static ToHistoryItem(item: any): HistoryItem | null {
        try {
            const result = new HistoryItem (
                item.id, 
                item.chatLink,
                item.name, 
                item.icon, 
                item.description,
                parseInt(item.type),
                parseInt(item.rarity),
                item.level,
                item.history
            );

            return result;
        } catch(e) {
            console.log(e);
            return null;
        }
    }

    static ToHistoryItems(items: any): HistoryItem[] {
        const arr: HistoryItem[] = [];
        items.forEach((item: any) => {
            const itemResult = HistoryItemFactory.ToHistoryItem(item);
            if (itemResult != null)
                arr.push(itemResult);
        });

        return arr;
    }
}