import { ItemRarity } from "../utils/ItemRarity";
import { ItemType } from "../utils/ItemType";

export type PricedItem = {
    Id: number;
    ChatLink: string;
    Name: string;
    Icon: string;
    Description: string;
    Type: ItemType;
    Rarity: ItemRarity;
    Level: number;

    BuyPrice: number;
    SellPrice: number;
    Appeal: number;
    Margin: number;
    Sold: number;
    Bought: number;
}

export class PricedItemFactory {
    static ToPricedItem(item: any): PricedItem | null {
        try {
            const result: PricedItem = { 
                Id: item.id, 
                ChatLink: item.chatLink,
                Name: item.name, 
                Icon: item.icon, 
                Description: item.description,
                Type: parseInt(item.type),
                Rarity: parseInt(item.rarity),
                Level: item.level,

                BuyPrice: item.buyPrice,
                SellPrice: item.sellPrice,
                Appeal: parseFloat(parseFloat(item.appeal).toFixed(2)),
                Margin: parseFloat(parseFloat(item.margin).toFixed(2)),
                Sold: parseFloat(parseFloat(item.sold).toFixed(2)),
                Bought: parseFloat(parseFloat(item.bought).toFixed(2))
            };

            return result;
        } catch(e) {
            console.log(e);
            return null;
        }
    }

    static ToPricedItems(items: any): PricedItem[] {
        const arr: PricedItem[] = [];
        items.forEach((item: any) => {
            const itemResult = PricedItemFactory.ToPricedItem(item);
            if (itemResult != null)
                arr.push(itemResult);
        });

        return arr;
    }
}