import { ItemField, ItemFieldSort } from "../utils/ItemField";
import { ItemRarity } from "../utils/ItemRarity";
import { ItemType } from "../utils/ItemType";

export class ItemQuery {
    public name: string;
    public type: ItemType;
    public rarity: ItemRarity;
    public minLevel: number;
    public maxLevel: number;
    public minBuyPrice: number;
    public maxBuyPrice: number;
    public minSellPrice: number;
    public maxSellPrice: number;
    public sortBy: ItemField;
    public sortDirection: ItemFieldSort;

    constructor(options?: ItemQueryOptions) {
        this.name = options?.name ?? "";
        this.type = options?.type ?? ItemType.All;
        this.rarity = options?.rarity ?? ItemRarity.All;
        this.minLevel = options?.minLevel ?? 0;
        this.maxLevel = options?.maxLevel ?? 80;
        this.minBuyPrice = options?.minBuyPrice ?? 0;
        this.maxBuyPrice = options?.maxBuyPrice ?? 100000000;
        this.minSellPrice = options?.minSellPrice ?? 0;
        this.maxSellPrice = options?.maxSellPrice ?? 100000000;
        this.sortBy = options?.sortBy ?? ItemField.Appeal;
        this.sortDirection = options?.sortDirection ?? ItemFieldSort.Descending;
    }

    toObject(): { [key: string]: string } {
        return {
            name: this.name,
            type: this.type.valueOf().toString(),
            rarity: this.rarity.valueOf().toString(),
            minLevel: this.minLevel.toString(),
            maxLevel: this.maxLevel.toString(),
            minBuyPrice: this.minBuyPrice.toString(),
            maxBuyPrice: this.maxBuyPrice.toString(),
            minSellPrice: this.minSellPrice.toString(),
            maxSellPrice: this.maxSellPrice.toString(),
            sortBy: this.sortBy.valueOf().toString(),
            sortDirection: this.sortDirection.valueOf().toString()
        };
    }
}

export interface ItemQueryOptions {
    name?: string;
    type?: ItemType;
    rarity?: ItemRarity;
    minLevel?: number;
    maxLevel?: number;
    minBuyPrice?: number;
    maxBuyPrice?: number;
    minSellPrice?: number;
    maxSellPrice?: number;
    sortBy?: ItemField;
    sortDirection?: ItemFieldSort;
}